import react from "react";
import "./add.css";
const axios = require("axios");
class add extends react.Component {
  state = {
    movie: ""
  };

  handlechange = (event) => {
    this.setState({ movie: event.target.value });
  };
  handlesend = (movie) => {
    axios
      .post("https://jsonplaceholder.typicode.com/posts", {
        id: 1,
        title: movie
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
    this.setState({ movie: "" });
  };
  render() {
    const { movie } = this.state;
    return (
      <div className="add">
        <h1>Movies</h1>
        <div>
          <input className="input" value={movie} onChange={this.handlechange} />
        </div>
        <button className="btn" onClick={() => this.handlesend(movie)}>
          send
        </button>
      </div>
    );
  }
}
export default add;
