import react from "react";
const axios = require("axios");
class list extends react.Component {
  state = {
    fetchdata: []
  };
  handlefetch = () => {
    const url = "https://jsonplaceholder.typicode.com/posts";
    axios.get(url).then((responce) => {
      const data = responce.data.map((fdata) => {
        return {
          id: fdata.id,
          title: fdata.title
        };
      });
      this.setState({ fetchdata: data });
    });
  };
  handledelete = (id) => {
    const url = "https://jsonplaceholder.typicode.com/posts/" + id;
    axios
      .delete(url, {
        data: {
          id: 2,
          title: "qui est esse"
        }
      })
      .then((responce) => {
        console.log(responce);
      });
  };
  handleupdate = (eid) => {
    const url = `https://jsonplaceholder.typicode.com/posts/${eid}`;
    axios
      .put(url, {
        id: eid,
        title: "foo",
        body: "bar",
        userId: 1
      })
      .then((response) => response);
  };

  render() {
    return (
      <div className="list">
        <h1>list</h1>
        <button className="btn" onClick={this.handlefetch}>
          fetch
        </button>
        <table>
          <thead>
            <tr>
              <th>id</th>
              <th>name</th>
              <th>delete</th>
              <th>update</th>
            </tr>
          </thead>
          <tbody>
            {this.state.fetchdata.map((data) => (
              <tr key={data.id}>
                <th>{data.id}</th>
                <th>{data.title}</th>
                <th>
                  <button
                    className="btn"
                    onClick={() => this.handledelete(data.id)}
                  >
                    delete
                  </button>
                </th>
                <th>
                  <button
                    className="btn"
                    onClick={() => this.handleupdate(data.id)}
                  >
                    update
                  </button>
                </th>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}
export default list;
