import { Link } from "react-router-dom";
import "./navbar.css";
const navbar = (props) => {
  return (
    <div>
      <Link className="link" to="/welcome">
        Welcome
      </Link>
      <Link className="link" to="/add">
        Add
      </Link>
      <Link className="link" to="/list">
        List
      </Link>
      <section>{props.children}</section>
    </div>
  );
};
export default navbar;
